.buttombox {
  float: left;
  width: 2319px;
  height: 830px;
}
.buttombox .tongji {
  width: 384px;
  height: 103px;
  text-align: center;
}
.buttombox .tongji a {
  font-size: 44px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 103px;
  letter-spacing: 2px;
  color: #ffffff;
  text-decoration: none;
  text-shadow: 0 0 30px #f4faf5;
}
.buttombox .buttomb {
  overflow: hidden;
  margin-top: 30px;
  width: 2319px;
  height: 689px;
  border-image-repeat: stretch;
}
.buttombox .buttomb .buttomleft {
  overflow: hidden;
  width: 22.3%;
  height: 78%;
  float: left;
  margin-left: 61px;
  margin-top: 88px;
}
.buttombox .buttomb .buttomleft .electric {
  overflow: hidden;
  margin-bottom: 50px;
  width: 516px;
  height: 149px;
}
.buttombox .buttomb .buttomleft .electric p {
  width: 100%;
  font-size: 36px;
  margin-bottom: 40px;
  color: #ffffff;
}
.buttombox .buttomb .buttomleft .electric p span {
  font-size: 72px;
  font-weight: 700;
  color: #00ffff;
}
.buttombox .buttomb .buttomleft .electric .call1 {
  margin-left: 183px;
}
.buttombox .buttomb .buttomleft .electric .call2 {
  margin-left: 146px;
}
.buttombox .buttomb .buttomleft .electric .call3 {
  margin-left: 79px;
}
.buttombox .buttomb .buttomcenter {
  float: left;
  margin-top: 138px;
  margin-left: 62px;
  width: 799px;
  height: 456px;
}
.buttombox .buttomb .buttomcenter .tatistics {
  width: 100%;
  height: 100%;
}
.buttombox .buttomb .buttomright {
  float: left;
  margin-top: 81px;
  margin-left: 30px;
  width: 792px;
  height: 565px;
}
.buttombox .buttomb .buttomright .zhemian {
  width: 672px;
  height: 445px;
}
.buttombox .buttomb .buttomright .zhemian .satisfied {
  width: 100%;
  height: 100%;
}
.buttombox .buttomb .buttomright .manyi {
  width: 792px;
  height: 39px;
}
.buttombox .buttomb .buttomright .manyi p {
  float: left;
  font-size: 40px;
  letter-spacing: 2px;
  color: #ffffff;
  margin: 0;
}
.buttombox .buttomb .buttomright .manyi ul {
  float: left;
}
.buttombox .buttomb .buttomright .manyi ul li {
  list-style: none;
  float: left;
  width: 71px;
  height: 44px;
  margin-left: 15px;
}
.buttombox .buttomb .buttomright .manyi span {
  display: inline-block;
  float: right;
  font-size: 40px;
  letter-spacing: 2px;
  color: #f8a036;
}
